import React, { Component } from "react";
import { Card, Table, message, Icon, Input, Select, Modal, Button } from "antd";
import { formateDate } from "../../../utils/dateUtils";
import {
  reqOrder_list,
  downloadList,
  orderReviewEdit
} from "../../../api/index";
import LinkButton from "../../../components/link-button";
import MyDatePicker from "../../../components/MyDatePicker";
import Diaodan from "./editData";

class Order_list extends Component {
  constructor(props) {
    super(props);
    this.reqData = {
      start_time: "",
      end_time: "",
      order_status: "",
      type: ""
    };
    this.inputKey = "";
    this.inputValue = "";
    this.state = {
      data: [],
      count: 0,
      pageSize: 20,
      isQueryShow: false,
      isEditShow: false
    };
  }
  getUsers = async (page, limit, reqData) => {
    const result = await reqOrder_list(page, limit, reqData);
    if (result.data) {
      this.setState({
        data: result.data,
        count: parseInt(result.count)
      });
    }
  };
  PlayerLossOrderSearch = async () => {
    this.setState({ isQueryShow: true });
  };
  onSearchData = (page, limit) => {
    //
    let reqData = {
      ...this.reqData
    };
    if (this.inputKey === "1" || this.inputKey === "2") {
      reqData.time_type = this.inputKey;
    } else if (this.inputKey) {
      reqData[this.inputKey] = this.inputValue;
    }
    this.getUsers(page, limit, reqData);
  };
  download = () => {
    let data = {
      ...this.reqData,
      inputParam: this.inputValue,
      paramKey: this.inputKey
    };
    downloadList(data);
  };
  componentDidMount() {
    this.getUsers(1, 20);
  }
  render() {
    return (
      <Card
        title={
          <div>
            <div>
              <Select
                placeholder=""
                style={{ width: 120 }}
                onSelect={value => (this.inputKey = value)}
              >
                <Select.Option value="order_id">id</Select.Option>
                <Select.Option value="user_id">user_id</Select.Option>
                <Select.Option value="1"></Select.Option>
                <Select.Option value="2"></Select.Option>
              </Select>
              &nbsp; &nbsp;
              <Input
                type="text"
                placeholder=""
                style={{ width: 150 }}
                onChange={e => (this.inputValue = e.target.value)}
              />
              &nbsp; &nbsp;
              <MyDatePicker
                handleValue={val => {
                  this.reqData.start_time = val[0];
                  this.reqData.end_time = val[1];
                }}
              />
              &nbsp; &nbsp;
              <Select
                defaultValue=""
                style={{ width: 200 }}
                onSelect={value => (this.reqData.order_status = value)}
              >
                <Select.Option value=""></Select.Option>
                <Select.Option value="0"></Select.Option>
                <Select.Option value="1"></Select.Option>
                <Select.Option value="4"></Select.Option>
                <Select.Option value="5"></Select.Option>
                <Select.Option value="6"></Select.Option>
                <Select.Option value="7"></Select.Option>
                <Select.Option value="8"></Select.Option>
                <Select.Option value="9"></Select.Option>
                <Select.Option value="10"></Select.Option>
                <Select.Option value="11"></Select.Option>
              </Select>
              &nbsp; &nbsp;
              <Select
                defaultValue=""
                style={{ width: 120 }}
                onSelect={value => (this.reqData.type = value)}
              >
                <Select.Option value=""></Select.Option>
                <Select.Option value="0"></Select.Option>
                <Select.Option value="1">alipay</Select.Option>
                <Select.Option value="2"></Select.Option>
                <Select.Option value="3"></Select.Option>
                <Select.Option value="4"></Select.Option>
                <Select.Option value="6"></Select.Option>
                <Select.Option value="7"></Select.Option>
                <Select.Option value="8"></Select.Option>
                <Select.Option value="9"></Select.Option>
              </Select>
              &nbsp; &nbsp;
              <LinkButton
                onClick={() => this.onSearchData(1, 20)}
                size="default"
              >
                <Icon type="search" />
              </LinkButton>
              &nbsp; &nbsp;
            </div>
            <div style={{ marginTop: 10 }}>
              <LinkButton onClick={this.PlayerLossOrderSearch} size="default">
                <Icon type="search" />
                
              </LinkButton>
            </div>
          </div>
        }
        extra={
          <span>
            <LinkButton
              style={{ float: "right" }}
              onClick={() => window.location.reload()}
              icon="reload"
              size="default"
            />
            <br />
            <br />
            <LinkButton
              size="default"
              style={{ float: "right" }}
              onClick={this.download}
              icon="download"
            ></LinkButton>
          </span>
        }
      >
        <Table
          bordered
          size="small"
          rowKey="id"
          dataSource={this.state.data}
          columns={this.initColumns()}
          pagination={{
            defaultPageSize: this.state.pageSize,
            showSizeChanger: true,
            showQuickJumper: true,
            showTotal: (total, range) => `${total}`,
            defaultCurrent: 1,
            total: this.state.count,
            onChange: (page, pageSize) => {
              this.onSearchData(page, pageSize);
            },
            onShowSizeChange: (current, size) => {
              this.setState({
                pageSize: size
              });
              this.onSearchData(current, size);
            }
          }}
          scroll={{ x: 2000 }}
        />
        <Modal
          title=""
          visible={this.state.isQueryShow}
          onCancel={() => {
            this.setState({ isQueryShow: false });
          }}
          footer={null}
          width="70%"
        >
          <Diaodan />
        </Modal>
        <Modal
          title=""
          visible={this.state.isEditShow}
          onCancel={() => {
            this.setState({ isEditShow: false });
          }}
          footer={null}
          width="50%"
        >
          <div>
            <div>ID{this.state.editUser_id}</div>
            <br />
            <div>{this.state.editMount}</div>
            <br />
            <div>
              
              <Button type="primary" onClick={() => this.orderReview(1)}>
                
              </Button>
              <Button type="primary" onClick={() => this.orderReview(0)}>
                
              </Button>
            </div>
          </div>
        </Modal>
      </Card>
    );
  }
  initColumns = () => [
    {
      title: "ID",
      dataIndex: "order_id",
      width: 320
    },
    {
      title: "user_id",
      dataIndex: "user_id"
    },
    {
      title: "",
      dataIndex: "user_name"
    },
    {
      title: "",
      dataIndex: "package_nick"
    },
    {
      title: "",
      dataIndex: "proxy_user_id"
    },
    {
      title: "",
      dataIndex: "channel_type",
      render: (text, record, index) => {
        let word;
        switch (text) {
          case "0":
            word = record.replace_id;
            break;
          case "5":
            word = "UC";
            break;
          case "12":
            word = "onePay";
            break;
          case "11":
            word = "";
            break;
          case "10":
          case "13":
          case "14":
          case "15":
          case "16":
            word = "";
            break;
          default:
            word = "";
            break;
        }
        return <span>{word}</span>;
      }
    },
    {
      title: "",
      dataIndex: "type",
      render: (text, record, index) => {
        let word;
        switch (text) {
          case "1":
            word = "alipay";
            break;
          case "2":
            word = "";
            break;
          case "3":
            word = "";
            break;
          case "4":
            word = "";
            break;
          case "5":
            word = "";
            break;
          case "6":
            word = "";
            break;
          case "7":
            word = "";
            break;
          case "8":
            word = "";
            break;
          case "9":
            word = "";
            break;
          default:
            word = "";
            break;
        }
        return <span>{word}</span>;
      }
    },
    {
      title: "",
      dataIndex: "amount",
      sorter: (a, b) => a.amount - b.amount,
      render: text => {
        text = parseInt(text).toFixed(2);
        return <span>{text}</span>;
      }
    },
    {
      title: "",
      dataIndex: "arrival_amount",
      sorter: (a, b) => a.arrival_amount - b.arrival_amount,
      render: text => {
        text = parseInt(text).toFixed(2);
        return <span>{text}</span>;
      }
    },
    {
      title: "",
      dataIndex: "status",
      render: (text, record, index) => {
        let word;
        switch (text) {
          case "1":
            word = "";
            break;
          case "2":
            word = "";
            break;
          case "3":
            word = "";
            break;
          case "4":
            word = "";
            break;
          case "5":
            word = "";
            break;
          case "6":
            word = "";
            break;
          case "7":
            word = "";
            break;
          case "8":
            word = "";
            break;
          case "9":
            word = "";
            break;
          case "10":
            word = "";
            break;
          case "11":
            word = "";
            break;
          case "12":
            word = "";
            break;
          default:
            word = "";
            break;
        }
        return <span>{word}</span>;
      }
    },
    {
      title: "",
      dataIndex: "created_at",
      render: formateDate,
      width: 200,
      sorter: (a, b) => a.created_at - b.created_at
    },
    {
      title: "",
      dataIndex: "arrival_at",
      width: 200,
      render: (text, record, index) => {
        if (text === "0" || !text) {
          return "";
        } else return formateDate(text);
      },
      sorter: (a, b) => a.arrival_at - b.arrival_at
    },
    {
      title: "",
      dataIndex: "",
      render: (text, record, index) => {
        if (record.status === "7") {
          return (
            <LinkButton size="small" onClick={() => this.edit(record)}>
              
            </LinkButton>
          );
        } else {
          return;
        }
      }
    },
    {
      title: "",
      dataIndex: "remark"
    }
  ];
  edit = record => {
    this.setState({
      isEditShow: true,
      editUser_id: record.user_id,
      editMount: record.amount
    });
    this.user_id = record.user_id;
    this.order_id = record.order_id;
    this.editType = record.type;
  };
  orderReview = async action => {
    let reqData = {
      user_id: this.user_id,
      order_id: this.order_id,
      type: this.editType,
      status: action === 1 ? 9 : 8,
      review_type: 2
    };
    const res = await orderReviewEdit(reqData);
    if (res.status === 0) {
      message.success(res.msg || "");
      window.location.reload();
    } else {
      message.error(res.msg || "");
    }
  };
}

export default Order_list;
